
import { booleanLiteral, validate } from "@babel/types"
import { isEmailIdentifier } from "firebase-admin/lib/auth/identifier"
import { idText } from "typescript"
import Vue from "vue"
import { MessageClient } from "cloudmailin"

// set $fetchState to true untill all the pictures are loaded in the page
export default Vue.extend({
    data() {
        return {
            viewed: true,
            isDisabled: true,
            // id of the email input
            email: "",
            name: "",
            message: "",
            isemail: true,
            isname: true,
            ismessage: true,
        }
    },
  head() {
    const title = "Reqest CV"
    const description =
      "Request a CV from me, I will send you a CV in your email."

    return {
      title,
      meta: this.$prepareMeta({
        title,
        description,
        keywords: "Request CV, Curriculum Vitae, Resume",
        url: "https://bitstore.dev/requestcv",
      }),
      link: [
        {
          rel: "canonical",
          href: "https://bitstore.dev/requestcv",
        },
      ],
    }
  },
    methods: {
        setPopup() {
            // check if the button is clickable
            if (this.isDisabled === false) {
                this.viewed = !this.viewed
                console.log(this.viewed)
                // send the email
                this.sendEmail()
                // Vue router push to home page after 1 second
                setTimeout(() => {
                    this.$router.push({ path: "/" });
                }, 2000);
            }
        },
        async sendEmail() {
            const client = new MessageClient({ username: "710506dea731b2f9", apiKey: "i9b1YzNd7HHSvZuT5YtTZSjE" });
            const response = await client.sendMessage({
                to: this.email,
                from: 'info@bitstore.dev',
                plain: 'test message',
                html: '<h1>Test Message</h1>',
                subject: "hello world"
            });
        }
    },
    watch: {
        email: function () {
            // check if the email is valid
            if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email)) {
                this.isemail = false;
            } else {
                this.isemail = true;
            }
            // check if all the inputs are valid
            if (this.isname === false && this.isemail === false && this.ismessage === false) {
                this.isDisabled = false;
            } else {
                this.isDisabled = true;
            }
        },
        name: function () {
            // check if the name is valid
            if (this.name.length > 2) {
                this.isname = false;
            } else {
                this.isname = true;
            }
            // check if all the inputs are valid
            if (this.isname === false && this.isemail === false && this.ismessage === false) {
                this.isDisabled = false;
            } else {
                this.isDisabled = true;
            }
            
        },
        message: function () {
            // check if the message is valid
            if (this.message.length > 2) {
                this.ismessage = false;
            } else {
                this.ismessage = true;
            }
            // check if all the inputs are valid
            if (this.isname === false && this.isemail === false && this.ismessage === false) {
                this.isDisabled = false;
            } else {
                this.isDisabled = true;
            }
        },
    }
})
